body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


section#main-page {
  text-align: center;
  background-color: #4a4acd;
  background: url(/nasa_image_search/static/media/na_stars.cd53f581.jpg);
  background-size: 100% auto;
  padding: 150px;
  opacity: 1;
  background-position: center;
  background-attachment: fixed;
  min-width: 80%;
  max-width: 100%;
  min-height: 400px;
}

#name {
  text-decoration: none;
  color: white;
  font-size: 50px;
  font-weight: 900; 
}

input#app-search-bar {
  width: 450px;
  height: 30px;
  font-size: 20px;
  border: 3px solid black;
  border-radius: 14px;
}

button.main-submit {
  height: 50px;
  width: 190px;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 60%;
  margin: 20px 10px;
  border: 3px solid black;
  border-radius: 14px;
}

button.main-submit:hover {
  cursor: pointer;
}

/* Styles for the modal */
.overlay {
  height: 0;
  width: 100%;
  position: auto;
  z-index: 1;
  left: 0;
  background-color: rgba(18, 17, 17, 0.9);
  overflow-y: hidden;
  transition: 500ms;
}

.overlay-content {
  position: relative;
  top: 0;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 26px;
  color: #e3e3e3;
  display: block;
  transition: 300ms;
}

.overlay a:hover, .overlay a:focus {
  color: #52a9f5;
}

.overlay .closebutton {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}

@media screen and (max-height: 450px) {
  .overlay {overflow-y: auto};
  .overlay a {font-size: 20px}
  .overlay .closebutton {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}

.specify {
  width: 30%;
  height: 20px;
  font-size: 16px;
}

.year-slider {
  position: relative;
  width: 1000px;
  height: 35px;
  text-align: center;
  color: #e3e3e3;
}

#specify-button { 
  width: 80px;
  height: 30px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}
table#gallery {
  margin: 15px 0px;
  float: center;
  width: 100%;
}

table#gallery img {
  width: 100%;
  height: auto;
  display: block;
  border: 1px solid #000;
  border-radius: 14px;
}

.galleryDivider {
  border: 1px solid rgb(28, 27, 27);
  background-color: rgba(244, 237, 237, 0.75);
  border-radius: 14px;
  margin: 3px 3px;
  transition: 300ms;
  text-align: center;
}

.galleryDivider:hover {
  box-shadow: 10px 10px 153px #e4dada;
  cursor: pointer;
  opacity: 0.8;
}

/* Building the modal */
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 200%;
  overflow: auto;
  scroll-behavior: smooth;
  background-color: rgba(0, 0, 0, 0.906);
}

#caption {
  float: right;
  display: block;
  position: auto;
  width: 80%;
  max-width: 700px;
  height: 100%;
  text-align: center;
  color: white;
  padding: 0px 100px;
  white-space: pre-line;
  word-wrap: break-word;
  object-fit: contain;
}

.photo_modal_img {
  margin: 0px 40px;
  float: left;
  width: 50px;
  height: 50px;
  object-fit: scale-down;
}

.photo_modal_img, #caption {
  -webkit-animation-name: zoom;
          animation-name: zoom;
  -webkit-animation-duration: 600ms;
          animation-duration: 600ms;
  max-width: 40%;
}

@-webkit-keyframes zoom {
  from {-webkit-transform: scale(0);transform: scale(0)}
  to {-webkit-transform: scale(1);transform: scale(1)}
}

@keyframes zoom {
  from {-webkit-transform: scale(0);transform: scale(0)}
  to {-webkit-transform: scale(1);transform: scale(1)}
}

/* X button */
.close_modal {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 300ms;
  cursor: pointer;
}

.close:hover, .close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

@media only screen and (max-width: 700px) {
  .photo_modal_img {
    width: 100%;
  }
}

.Search-header {
  height: 150%;
